import { ComponentContainer } from '../ComponentContainer';
import {Column} from "../index";

/**
 * Row component
 *
 * @copyright Benoit Gauthier <bgauthier555@gmail.com>
 * @author Benoit Gauthier <bgauthier555@gmail.com>
 * @licence MIT
 * @class
 * @inheritdoc
 * @public
 */
class Row extends ComponentContainer {

    /**
     * Component constructor
     * @param {string} id Component unique id
     */
    constructor(id: string)
    {

        super(id);

        // noinspection HtmlUnknownAttribute
        this.template = '<div {attributes}>{child_items}</div>';
        this.componentClassName = 'Row';

    }

    /**
     * Returns component meta data
     * @returns {object}
     */
    public static getMetaData() : any
    {

        /**
         * Static
         * Component meta data information
         * category is one of layout | content | component
         * @type {object}
         */
        return {
            name : 'Row',
            description : 'Rows are wrappers for columns.',
            category : 'layout',
            libraries : {
                Bootstrap_4: {
                    supported: true,
                    comments : '',
                },
                Html5: {
                    supported : false,
                    comments : '',
                }
            },
            example : '@see Container example',
        };


    }

    /**
     * Adds a column to the row
     * @param id
     * @param size
     * @returns {*}
     */
    public addColumn(id: string, size: string) : Column
    {
        let oItem = new Column(id);
        oItem.setSize(size);
        this.addItem(oItem);
        return oItem;
    }


}


export { Row };